header {
  position: fixed;
  top: 0;
  width: 100%;
}
header .header__wrapper {
  position: relative;
}
header .header__wrapper .header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header__wrapper .header__container::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 167px;
  width: 50%;
  height: 1px;
  background-color: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.25;
}
header .header__wrapper .header__container .header__logo {
  margin-left: 55px;
  margin-right: 20px;
}
header .header__wrapper .header__container .header__links {
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(81.5485px);
  padding: 40px 123px 40px 165px;
  display: flex;
  gap: 48px;
  align-items: center;
  flex-direction: row;
}
header .header__wrapper .header__container .header__links .links__item {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #FFFFFF;
  cursor: pointer;
  position: relative;
}
header .header__wrapper .header__container .header__links .links__item span {
  font-weight: 700;
}
header .header__wrapper .header__container .header__links .links__item:hover::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -40px;
  width: 100%;
  height: 3px;
  background-color: #FFFFFF;
}
header .header__wrapper .header__container .header__links .links__item:active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -40px;
  width: 100%;
  height: 3px;
  background-color: #FFFFFF;
}
header .header__wrapper .header__container .header__hamburger {
  display: none;
}

@media only screen and (max-width: 1024px) {
  header .header__wrapper .header__container::after {
    content: none;
  }
  header .header__wrapper .header__container .header__links .links__item span {
    display: none;
  }
}
@media only screen and (max-width: 768px) {
  header .header__wrapper .header__container {
    padding: 24px 24px;
  }
  header .header__wrapper .header__container::after {
    display: none;
  }
  header .header__wrapper .header__container .header__logo {
    margin: 0;
  }
  header .header__wrapper .header__container .header__links {
    display: none;
  }
  header .header__wrapper .header__container .header__hamburger {
    display: block;
  }
  header .header__wrapper .header__container .header__hamburger div {
    width: 24px;
    height: 3px;
    background-color: #D0D6F9;
  }
  header .header__wrapper .header__container .header__hamburger div + div {
    margin-top: 6px;
  }
  header.openmenu .header__wrapper .header__container .header__links {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    max-width: 254px;
    width: 100%;
    flex-direction: column;
    padding-top: 118px;
    padding-left: 32px;
    padding-right: 0;
    align-items: baseline;
    height: 100vh;
  }
  header.openmenu .header__wrapper .header__container .header__hamburger {
    display: block;
    z-index: 5;
  }
  header.openmenu .header__wrapper .header__container .header__hamburger div {
    width: 24px;
    height: 3px;
    background-color: #D0D6F9;
  }
  header.openmenu .header__wrapper .header__container .header__hamburger div + div {
    margin-top: 6px;
  }
}
@media only screen and (max-width: 1585px) {
  header .header__wrapper .header__container:after {
    width: 40%;
  }
}
@media only screen and (max-width: 1321px) {
  header .header__wrapper .header__container:after {
    width: 30%;
  }
}
@media only screen and (max-width: 1142px) {
  header .header__wrapper .header__container:after {
    width: 20%;
  }
}
